* {
  margin: 0;
  padding: 0;
}

body {
  padding: 1em;
  font-family: sans-serif;
}

.tpl ul {
  list-style-type: none;
}

.tpl input[type=radio] {
  position: absolute;
  left: -9999px;
  top: -9999px;
}

.tpl .tabs {
  position: relative;
  z-index: 999;
  height: 42px;
  white-space: nowrap;
  font-size: 0;
}

.tpl .tabs label {
  display: inline-block;
  vertical-align: bottom;
  margin-right: 4px;
  margin-bottom: -1px;
  margin-left: -1px;
  padding: 12px 24px; 
  border: solid 1px;
  border-radius: 5px 5px 0 0;
  cursor: pointer;
  text-transform: uppercase;
  font-size: 11px;
  letter-spacing: 1px;
  box-shadow: 0 -1px 6px rgba(0,0,0,0.1) inset;
}

.tpl .tabs label:first-child {
  margin-left: 0;
}

.tpl .tabs label:hover {}

.tpl input:nth-child(1):checked ~ .tabs label:nth-child(1),
.tpl input:nth-child(2):checked ~ .tabs label:nth-child(2),
.tpl input:nth-child(3):checked ~ .tabs label:nth-child(3),
.tpl input:nth-child(4):checked ~ .tabs label:nth-child(4) {
  padding: 14px 24px;
  box-shadow: none;
}

.tpl .sections {}

.tpl .sections li {
  display: none;
  height: 180px;
  padding: 1em;
  border: solid 1px #DDD;
  border-radius: 0 5px 5px 5px;
  background-color: #FFF;
  box-shadow: 1px 1px 20px rgba(0,0,0,0.4);
}

.tpl input:nth-child(1):checked ~ .sections li:nth-child(1),
.tpl input:nth-child(2):checked ~ .sections li:nth-child(2),
.tpl input:nth-child(3):checked ~ .sections li:nth-child(3),
.tpl input:nth-child(4):checked ~ .sections li:nth-child(4) {
  display: block;
}